import fs from 'fs'
import path from 'path'

export interface CSVProduct {
  id: string
  name: string
  category: string
  brand: string
  price: number
  rating: number
  reviews_count: number
  source: string
  url: string
  description: string
  image_url: string
  date_added: string
  extracted_price: number
  reviews: string
  thumbnail: string
  serpapi_product_api: string
}

export function parseCSVProducts(): CSVProduct[] {
  try {
    const csvPath = path.join(process.cwd(), 'products_rows.csv')
    const csvContent = fs.readFileSync(csvPath, 'utf-8')
    
    const lines = csvContent.split('\n').filter(line => line.trim())
    const headers = lines[0].split(',').map(header => header.replace(/"/g, '').trim())
    
    const products: CSVProduct[] = []
    
    for (let i = 1; i < lines.length; i++) {
      const values = parseCSVLine(lines[i])
      
      if (values.length >= headers.length) {
        const product: CSVProduct = {
          id: values[0] || '',
          name: values[1] || '',
          category: values[2] || '',
          brand: values[3] || '',
          price: parseFloat(values[4]) || 0,
          rating: parseFloat(values[5]) || 0,
          reviews_count: parseInt(values[6]) || 0,
          source: values[7] || '',
          url: values[8] || '',
          description: values[9] || '',
          image_url: values[10] || '',
          date_added: values[11] || '',
          extracted_price: parseFloat(values[12]) || 0,
          reviews: values[13] || '',
          thumbnail: values[14] || '',
          serpapi_product_api: values[15] || ''
        }
        products.push(product)
      }
    }
    
    console.log(`üìä Loaded ${products.length} products from CSV`)
    return products
  } catch (error) {
    console.error('‚ùå Error parsing CSV:', error)
    return []
  }
}

function parseCSVLine(line: string): string[] {
  const result: string[] = []
  let current = ''
  let inQuotes = false
  
  for (let i = 0; i < line.length; i++) {
    const char = line[i]
    
    if (char === '"') {
      inQuotes = !inQuotes
    } else if (char === ',' && !inQuotes) {
      result.push(current.trim())
      current = ''
    } else {
      current += char
    }
  }
  
  result.push(current.trim())
  return result
}

export function searchCSVProducts(query: string, products: CSVProduct[]): CSVProduct[] {
  const searchTerms = query.toLowerCase().split(' ').filter(term => term.length > 2)
  
  return products.filter(product => {
    const searchableText = [
      product.name,
      product.category,
      product.brand,
      product.description
    ].join(' ').toLowerCase()
    
    return searchTerms.some(term => searchableText.includes(term))
  })
}

export function rankProducts(products: CSVProduct[]): CSVProduct[] {
  return products.sort((a, b) => {
    // Primary sort: by price (ascending - cheaper first)
    const priceA = a.extracted_price || a.price || 0
    const priceB = b.extracted_price || b.price || 0
    
    if (priceA !== priceB) {
      return priceA - priceB
    }
    
    // Secondary sort: by rating (descending - higher rating first)
    const ratingA = a.rating || 0
    const ratingB = b.rating || 0
    
    if (ratingA !== ratingB) {
      return ratingB - ratingA
    }
    
    // Tertiary sort: by reviews count (descending - more reviews first)
    const reviewsA = a.reviews_count || 0
    const reviewsB = b.reviews_count || 0
    
    return reviewsB - reviewsA
  })
}
