import { createClient } from '@supabase/supabase-js'

const SUPABASE_URL = process.env.NEXT_PUBLIC_SUPABASE_URL || process.env.REACT_APP_SUPABASE_URL
const SUPABASE_ANON_KEY = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY || process.env.REACT_APP_SUPABASE_KEY

if (!SUPABASE_URL || !SUPABASE_ANON_KEY) {
  console.warn('‚ö†Ô∏è Missing Supabase environment variables')
}

export const supabase = createClient(SUPABASE_URL || '', SUPABASE_ANON_KEY || '')

export interface SupabaseProduct {
  id?: string
  name: string
  category: string
  brand: string
  price: number
  rating: number
  reviews_count: number
  source: string
  url: string
  description: string
  image_url: string
  date_added: string
  extracted_price: number
  reviews: string
  thumbnail: string
  serpapi_product_api: string
}

export async function insertProductsToSupabase(products: SupabaseProduct[]): Promise<boolean> {
  try {
    if (!SUPABASE_URL || !SUPABASE_ANON_KEY) {
      console.warn('‚ö†Ô∏è Supabase not configured, skipping database insert')
      return false
    }

    const { data, error } = await supabase
      .from('products')
      .insert(products.map(product => ({
        ...product,
        date_added: new Date().toISOString()
      })))

    if (error) {
      console.error('‚ùå Supabase insert error:', error)
      return false
    }

    console.log(`‚úÖ Inserted ${products.length} products to Supabase`)
    return true
  } catch (error) {
    console.error('‚ùå Supabase error:', error)
    return false
  }
}

export async function searchSupabaseProducts(query: string): Promise<SupabaseProduct[]> {
  try {
    if (!SUPABASE_URL || !SUPABASE_ANON_KEY) {
      console.warn('‚ö†Ô∏è Supabase not configured, returning empty results')
      return []
    }

    const { data, error } = await supabase
      .from('products')
      .select('*')
      .or(`name.ilike.%${query}%,category.ilike.%${query}%,brand.ilike.%${query}%,description.ilike.%${query}%`)
      .limit(20)

    if (error) {
      console.error('‚ùå Supabase search error:', error)
      return []
    }

    console.log(`üîç Found ${data?.length || 0} products in Supabase`)
    return data || []
  } catch (error) {
    console.error('‚ùå Supabase search error:', error)
    return []
  }
}

export async function getAllSupabaseProducts(): Promise<SupabaseProduct[]> {
  try {
    if (!SUPABASE_URL || !SUPABASE_ANON_KEY) {
      console.warn('‚ö†Ô∏è Supabase not configured, returning empty results')
      return []
    }

    const { data, error } = await supabase
      .from('products')
      .select('*')
      .limit(100)

    if (error) {
      console.error('‚ùå Supabase fetch error:', error)
      return []
    }

    console.log(`üìä Loaded ${data?.length || 0} products from Supabase`)
    return data || []
  } catch (error) {
    console.error('‚ùå Supabase fetch error:', error)
    return []
  }
}
